import { mapState, mapMutations, mapActions } from 'vuex'
import LoadingMore from '../../../components/loadingMoreTemplate'
import { upyunUrl } from '../../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      isAddressBack: false,
      page: 1
    }
  },
  onLoad (options) {
    this.isAddressBack = options.isAddressBack
  },
  onShow () {
    this.getAddressListFn(true)
  },
  // 下拉刷新
  onPullDownRefresh () {
    this.page = 1
    this.getAddressListFn(true)
  },
  // 上拉加载
  onReachBottom () {
    this.page += 1
    this.getAddressListFn(false)
  },
  computed: {
    ...mapState([
      'userInfo',
      'addressList'
    ])
  },
  methods: {
    ...mapMutations({
      setAffirmSiteInfo: 'SET_AFFIRM_SITE_INFO',
      setAddressList: 'SET_ADDRESS_LIST'
    }),
    ...mapActions([
      'getAddressList',
      'selectAddress',
      'deletedAddressInfo'
    ]),
    // 获取地址列表
    getAddressListFn (isLoading) {
      let data = {
        that: this,
        page: this.page
      }
      if (isLoading) {
        this.page = 1
        this.setAddressList([])
      }
      uni.showNavigationBarLoading()
      this.getAddressList(data).then(() => {
        if (isLoading) uni.hideLoading()
        uni.hideNavigationBarLoading()
        uni.stopPullDownRefresh()
      })
    },
    // 删除地址列表
    onDeleteFn (id, index) {
      const that = this
      let data = {
        that: this,
        id
      }
      uni.showModal({
        content: '确定移除地址?',
        confirmColor: '#ff4040',
        success (res) {
          if (res.confirm) {
            that.deletedAddressInfo(data).then((res) => {
              if (res.ret === 1) {
                that.addressList.splice(index, 1)
                that.showToastFn('删除成功!')
              } else {
                that.showToastFn('删除失败!')
              }
            })
          }
        }
      })
    },
    // 选择地址
    onSelectAddressFn (item) {
      const that = this
      if (this.isAddressBack === 'true') {
        this.setAffirmSiteInfo(item)
        uni.showModal({
          content: '是否设置成默认地址?',
          cancelText: '否',
          confirmText: '是',
          confirmColor: '#e1564f',
          success (res) {
            if (res.confirm) that.onSetDefaultAddressFn(item.id)
            uni.navigateBack()
          }
        })
      } else {
        this.onSetDefaultAddressFn(item.id)
      }
    },
    // 设置默认地址
    onSetDefaultAddressFn (id) {
      this.selectAddress({ that: this, id }).then(() => {
        this.addressList.forEach((item) => {
          if (id === item.id) {
            item.is_default = '1'
            if (this.isAddressBack === 'false') this.showToastFn('设置为默认地址')
          } else {
            item.is_default = '0'
          }
        })
      })
    },
    // 编辑地址
    onSkipPageFn (data = '') {
      uni.navigateTo({
        url: `/pages/me/myAddress/addressDetails/index?data=${JSON.stringify(data)}`
      })
    },
    showToastFn (title) {
      uni.showToast({
        title,
        icon: 'none',
        duration: 2000
      })
    }
  },
  components: {
    LoadingMore
  }
}
